export default {
	data() {
		return {
			list:[],
		}
	},
	onLoad() {
		var t=this;	
		uni.showLoading();
		t.urlRequest.urlRequest(t.func.getActivityList,'',function(res){
			t.list=res.data.status_list
			uni.hideLoading();
		})
		
	},
	methods: {
		activeDetail:function(id){
			uni.navigateTo({
				url: '../index1/active-detail?id='+id
			})
		},
	}
}
